window.addEventListener('DOMContentLoaded', () => {
    $('input[type="tel"]').mask('+7 999 999-9999', { autoclear: false }, { placeholder: '+7             ' });
    fix100vh();
    window.addEventListener('resize', () => {
        fix100vh();
        getScrollBarSize()
    })

    getScrollBarSize()

    $(".info-card__show").click(function () {
        $(this).parents(".info-card").toggleClass("_active")
        $(this).siblings(".info-card__hidden").slideToggle()
    })

    setLabelPlace()

    let textareas = document.querySelectorAll("textarea");
    textareas.forEach(textarea => {
        textarea.addEventListener('input', autoResize, false);

        function autoResize() {
            this.style.height = 'auto';
            this.style.height = this.scrollHeight + 16 + 'px';
        }
    })

    let fileInput = document.querySelector("[data-upload-id='file-input']")

    if(fileInput){
        const upload = new FileUploadWithPreview.FileUploadWithPreview('file-input');

        upload.options.multiple = true


        $(document).on('change', '.input-container input', function(e) {
            let parent = $(this).parents(".file-input")
            $(parent).addClass("_uploaded")
        })


        $(document).on('click', '.image-preview-item-clear', function(e) {
            let count = $(this).parents(".file-input").find(".image-preview-item").length
            if(count === 1){
                $(this).parents(".file-input").removeClass("_uploaded")
            }
        })
    }

    popups()

    $("._review-submit").click(function (e) {
        let pattern = /^[a-z0-9_-]+@[a-z0-9-]+\.[a-z]{2,6}$/i;
        let form = $("._review-form")
        let name = $(form).find('[name="name"]')
        let email = $(form).find('[name="email"]')
        let impression = $(form).find('[name="impression"]')
        let unlike = $(form).find('[name="unlike"]')
        let like = $(form).find('[name="like"]')
        let phone = $(form).find('[name="lphoneike"]')



        let nameIsValid = false
        let emailIsValid = false
        let impressionIsValid = false

        if($(name).val() !== ""){
            $(name).removeClass("_error")
            $(name).addClass("_success")
            $(name).siblings(".fg__text").text("")
            nameIsValid = true
        } else{
            $(name).addClass("_error")
            $(name).removeClass("_success")
            $(name).siblings(".fg__text").text("Поле не должно быть пустым")
            nameIsValid = false
        }

        if($(email).val() !== ""){
            if($(email).val().search(pattern) == 0){
                $(email).removeClass("_error")
                $(email).addClass("_success")
                $(email).siblings(".fg__text").text("")
                emailIsValid = true
            }else{
                $(email).addClass("_error")
                $(email).removeClass("_success")
                $(email).siblings(".fg__text").text("Введите email")
                emailIsValid = false
            }
        } else{
            $(email).addClass("_error")
            $(email).removeClass("_success")
            $(email).siblings(".fg__text").text("Поле не должно быть пустым")
            emailIsValid = false
        }

        if($(impression).val() !== ""){
            $(impression).removeClass("_error")
            $(impression).addClass("_success")
            $(impression).siblings(".fg__text").text("")
            impressionIsValid = true
        } else{
            $(impression).addClass("_error")
            $(impression).removeClass("_success")
            $(impression).siblings(".fg__text").text("Поле не должно быть пустым")
            impressionIsValid = false
        }


        if(nameIsValid && emailIsValid && impressionIsValid){
            let data = {
                name: $(name).val(),
                phone: $(phone).val(),
                email: $(email).val(),
                like: $(like).val(),
                unlike: $(unlike).val(),
                impression: $(impression).val(),
            }

            console.log(data)
            $("#reviewModal .modal__dialog").addClass("_success")
        } else{
            console.log(false)
        }
    })



})

function fix100vh() {
    let vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

